import React, { useState, useEffect } from 'react';

const CountdownTimer = ({ targetDate }) => {
    const calculateTimeLeft = () => {
        const difference = +new Date(targetDate) - +new Date();
        let timeLeft = {};

        if (difference > 0) {
            timeLeft = {
                days: Math.floor(difference / (1000 * 60 * 60 * 24)),
                hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
                minutes: Math.floor((difference / 1000 / 60) % 60),
                seconds: Math.floor((difference / 1000) % 60),
            };
        } else {
            timeLeft = { days: 0, hours: 0, minutes: 0, seconds: 0 };
        }
        return timeLeft;
    };

    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());
    const [hasEnded, setHasEnded] = useState(false);

    useEffect(() => {
        const initialDiff = +new Date(targetDate) - +new Date();
        if (initialDiff <= 0) {
            setHasEnded(true);
            setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
            return;
        }

        const timer = setInterval(() => {
            const newTimeLeft = calculateTimeLeft();
            if (Object.values(newTimeLeft).every(val => val === 0)) {
                setTimeLeft(newTimeLeft);
                setHasEnded(true);
                clearInterval(timer);
            } else {
                setTimeLeft(newTimeLeft);
            }
        }, 1000);

        return () => clearInterval(timer);
    }, [targetDate]);

    const timerComponents = [];

    Object.keys(timeLeft).forEach((interval) => {
        timerComponents.push(
            <span key={interval} className="countdown-item mx-2">
                <div className="display-4 fw-bold text-primary">{timeLeft[interval]}</div>
                <div className="text-uppercase text-muted small">{interval}</div>
            </span>
        );
    });

    return (
        <div className="countdown-container text-center mt-5 p-4 bg-white rounded shadow-sm">
            <h3 className="mb-4 text-dark">Submission Deadline:</h3>
            {hasEnded ? (
                <p className="lead text-danger fw-bold">The deadline has expired</p>
            ) : (
                <div className="d-flex justify-content-center flex-wrap">
                    {timerComponents.length ? timerComponents : <span>...</span>}
                </div>
            )}
        </div>
    );
};

export default CountdownTimer;